@import url('https://fonts.googleapis.com/css2?family=Imperial+Script&family=Noto+Sans+Mono:wght@400;700&family=Nunito&family=Nunito+Sans:opsz@6..12&family=Pinyon+Script&family=Praise&family=Raleway:wght@400;700&display=swap')
*
    margin: 0
    padding: 0
html
    height: 100%
body
    height: 100%
    touch-action: none
    // background-color: gray
    -webkit-touch-callout: none
    -webkit-user-select: none
    -khtml-user-select: none
    -moz-user-select: none
    -ms-user-select: none
    user-select: none
    font-family: 'Raleway', sans-serif
    font-family: 'Nunito Sans', sans-serif
    font-family: 'Nunito', sans-serif
    font-family: 'Noto Sans Mono', monospace
    display: flex
    #root
        display: flex
        .App
            display: flex
            flex-direction: column
            // background-color: pink
            h1
                height: 40px
                display: flex
                align-items: center
                justify-content: center
                font-family: 'Imperial Script', cursive
                position: fixed
                width: 100%
                // font-family: 'Noto Sans Mono', monospace
                // font-family: 'Nunito', sans-serif
                // font-family: 'Nunito Sans', sans-serif
                // font-family: 'Pinyon Script', cursive
                // font-family: 'Praise', cursive
                // font-family: 'Raleway', sans-serif
                font-weight: normal
                font-size: 24px
                display: none
            p
                background-color: #fafafa
                // line-height: 2.5vh
                height: 80%
                overflow-y: scroll
                padding: 10px 50px 10px 0px
                scrollbar-width: 0
                display: flex
                justify-content: stretch
                flex-wrap: wrap
                align-content: flex-start
                font-size: 14px
                -ms-overflow-style: none
                scrollbar-width: none
                &::-webkit-scrollbar
                    display: none
                span
                    touch-action: none
                    background-color: #fff
                    // box-shadow: 0 0 10px #00000011
                    // border-radius: 100px
                    // touch-action: none
                    // display: inline-block
                    // background-color: tan
                    // padding-right: 9.61px
                    padding: 0 5px
                    // height: 5%
                    font-size: 14px
                    line-height: 25px
                    // background-color: pink
                &:last-of-type
                    height: 20%
                    padding: 5px
            .buttons
                text-align: center
                height: 5%
                // background-color: gold
                display: flex
                justify-content: center
                button
                    // height: 5%
                    padding: 0 30px
                    border: none
